.field {
  position: relative;
  max-width: 700px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border: 1px solid #000000;
}

.field__cell {
  -ms-flex-preferred-size: 10%;
      flex-basis: 10%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.field--game-over:before {
  content: 'Game over';
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 30px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
}



